<template>
    <div class="cableDuct">
        <div class="cableDuct_item1 ">
            <div class="bg">
                <div class="title">
                    <span></span>
                    <div class="title_name">烟感监测</div>
                </div>
                <div class="total1">
                    <div class="left1_item">
                        <div v-for="(item, i) in smokelist" :key="i">
                            <div class="left1_item_title"
                                :class="[item.smokeds == 1 ? 'otherActiveClass' : 'ActiveClass']">
                                <span class="cableDuct_img station-imgpostion img"
                                    :class="[item.smokeds == 1 ? 'cableDucterror_img' : 'cableDuct_img']"></span>
                                <span class="itemtitle">{{ item.device_name }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="bg">
                <div class="title">
                    <span></span>
                    <div class="title_name">水浸监测</div>
                </div>
                <div class="total2">
                    <div class="left1_item">
                        <div v-for="(item, i) in waterlist" :key="i">
                            <div class="left1_item_title"
                                :class="[item.waterimmersionms == 1 ? 'otherActiveClass' : 'ActiveClass']">
                                <span class="cableDuct_img station-imgpostion img"
                                    :class="[item.waterimmersionms == 1 ? 'cableDucterror_img' : 'cableDuct_img']"></span>
                                <span class="itemtitle">{{ item.device_name }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="cableDuct_item2 ">
            <div class="bg">
                <div class="title">
                    <span></span>
                    <div class="title_name">油池液位监测</div>
                </div>
                <div class="cableDuct_item2_title">
                    <div v-for="(item, i) in oilpoollevellist" :key="i">
                        <span class="">{{ item.device_name }}</span>
                        <span :class="[item.oilpoollevel < 3500 ? 'ActiveClass' : 'otherActiveClass']">{{
                                item.oilpoollevel
                        }}mm</span>
                    </div>
                </div>
            </div>
            <div class="bg">
                <div class="title">
                    <span></span>
                    <div class="title_name">油池油污监测</div>
                </div>
                <div class="cableDuct_radio">
                    <el-row>
                        <el-col :span="8" class="df">
                            <div class=" station-imgpostion cimg"
                                :class="[choice.includes('有水') !== true ? 'checked' : 'unchecked']">
                            </div>
                            <span
                                :class="[choice.includes('有水') !== true ? 'ActiveClass' : 'otherActiveClass']">有水</span>
                        </el-col>
                        <el-col :span="8" class="df"> <span class=" station-imgpostion cimg"
                                :class="[choice.includes('有油') !== true ? 'checked' : 'unchecked']"></span>
                            <span class=""
                                :class="[choice.includes('有油') !== true ? 'ActiveClass' : 'otherActiveClass']">有油</span>
                        </el-col>
                        <el-col :span="8" class="df"> <span class=" station-imgpostion cimg"
                                :class="[choice.includes('高油') !== true ? 'checked' : 'unchecked']"></span>
                            <span class=""
                                :class="[choice.includes('高油') !== true ? 'ActiveClass' : 'otherActiveClass']">高油</span>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8" class="df"> <span class=" station-imgpostion cimg"
                                :class="[choice.includes('空气') !== true ? 'checked' : 'unchecked']"></span>
                            <span class=""
                                :class="[choice.includes('空气') !== true ? 'ActiveClass' : 'otherActiveClass']">空气</span>
                        </el-col>
                        <el-col :span="8" class="df"> <span class=" station-imgpostion cimg"
                                :class="[choice.includes('信号过高告警') !== true ? 'checked' : 'unchecked']"></span>
                            <span class=""
                                :class="[choice.includes('信号过高告警') !== true ? 'ActiveClass' : 'otherActiveClass']">信号过高告警</span>
                        </el-col>
                        <el-col :span="8" class="df"> <span class=" station-imgpostion cimg"
                                :class="[choice.includes('信号过低告警') !== true ? 'checked' : 'unchecked']"></span>
                            <span class=""
                                :class="[choice.includes('信号过低告警') !== true ? 'ActiveClass' : 'otherActiveClass']">信号过低告警</span>
                        </el-col>
                    </el-row>
                    <el-row>油污厚度: <span class="span_num">{{ oilpollutionlist.reservoirthickness }}</span>mm</el-row>
                </div>
            </div>
        </div>
        <div class="cableDuct_item3 ">
            <div class="bg">
                <div class="title">
                    <span></span>
                    <div class="title_name">温湿度监测</div>
                </div>
                <div class="right">
                    <div class="right_item" v-for="(item, i) in templlist" :key="i">
                        <div>{{ item.device_name }}</div>
                        <div class="temp"> 温度:<span class="ctemp"
                                :class="[item.temp < 70 ? 'ctemp' : 'otherActiveClass']">{{ item.temp }}℃</span>
                            湿度：<span :class="[item.humidity < 99 ? 'ctemp' : 'otherActiveClass']" class="ctemp">{{
                                    item.humidity
                            }}%</span>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <div class="bg">
                    <div class="title">
                        <span></span>
                        <div class="title_name">水位监测</div>
                    </div>
                    <div class="cableDuct_item2_title">
                        <div v-for="(item, i) in waterlevelcheck" :key="i">
                            <span class="">{{ item.device_name }}</span>
                            <span :class="[item.waterlevel < 300 ? 'ActiveClass' : 'otherActiveClass']">{{
                                    item.waterlevel
                            }}m</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import cableDuct from "./cableDuct";
export default cableDuct;
</script>
<style scoped>
@import url("cableDuct.css");
@import url("../../assets/css/common.css");
</style>


