import {
  smoke_check,
  oilpoollevel_check,
  temp_check,
  waterimmersion_check,
  oilpollution_check,
  waterlevel_check
} from '../../utils/service';
import mqtt from "mqtt"
export default {
  created() {
    this.initMqtt()
  },
  components: {

  },
  data() {
    return {
      radio: '1',
      radio2: '',
      isActive: 0,
      smokelist: '',
      oilpoollevellist: '',
      templlist: '',
      waterlist: '',
      oilpollutionlist: '',
      choice: '',
      waterlevelcheck: '',
    };
  },

  mounted() {
    this.getsmokedata()
    this.initMqtt()
  },
  methods: {
    getsmokedata() {
      smoke_check({}, res => {
        this.smokelist = res.data
      })
      oilpoollevel_check({}, res => {
        this.oilpoollevellist = res.data
      })
      temp_check({}, res => {
        this.templlist = res.data
      })
      waterimmersion_check({}, res => {
        this.waterlist = res.data
      })
      oilpollution_check({}, res => {
        this.oilpollutionlist = res.data[0]
        this.choice = this.oilpollutionlist.oilpollutionmonitoring
      })
      waterlevel_check({}, res => {
        this.waterlevelcheck = res.data
        // console.log('this.waterlevelcheck :>> ', this.waterlevelcheck);
      })
    },
    initMqtt(topic) {
      const mqttConfig = {
        port: 8083,
        keepalive: 60,
        clientId: 'mqttjs_' + Math.random().toString(16).substr(2, 8),
        // protocolId: 'MQTT',
        // protocolVersion: 4,
        // clean: true,
      }
      var TopicList = {
        "smoke_check": "topic/mqtt/smoke_check",
        "oilpoollevelmd_check": "topic/mqtt/oilpoollevelmd_check",
        "temp_check": "topic/mqtt/temp_check",
        "waterimmersion_check": "topic/mqtt/waterimmersion_check",
        "oilpollution_check": "topic/mqtt/oilpollution_check",
        "waterlevel_check": "topic/mqtt/waterlevel_check"
      }
      const _this = this;
      //建立连接，需要搭建消息中间件服务器的（我这里是后台）提供用户名或密码
      const cfg = Object.assign(mqttConfig, {
        // username: 'xxxx1',
        // password: 'xxx2',
      });
      // 在data中定义MQTT,以便断开连接
      // ws://xxxxx是连接地址，后台提供，wss是https连接
      _this.MQTT = mqtt.connect('ws://8.142.173.95:8083/mqtt', cfg)
      _this.MQTT.on('error', (e) => {
        _this.MQTT.end();
      });
      //建立连接后订阅主题
      _this.MQTT.on('connect', () => {
        // 订阅一个主题
        for (let k in TopicList) {
          const element = TopicList[k];
          _this.MQTT.subscribe(element, {
            qos: 1
          }, err => {
            if (!err) {
              // console.info(' ---- 订阅成功')
            } else {
              // console.warn('订阅失败')
            }
          })
        }
      });
      // 后台发送的消息
      _this.MQTT.on('message', function (top, message) {
        //发送过来的如果是对象你是需要解析的。
        const res = JSON.parse(message.toString());
        const TOP = top;
        console.log(top + ': ', res);
        //根据不同主题进行赋值操作
        switch (TOP) {
          case "topic/mqtt/oilpoollevelmd_check":
            var slist = res.data
            _this.$nextTick(() => {
              _this.oilpoollevellist = slist
            })
            break;
          case "topic/mqtt/temp_check":
            var slist = res.data
            _this.$nextTick(() => {
              _this.templlist = slist
            })
            break;
          case "topic/mqtt/waterimmersion_check":
            var slist = res.data
            _this.$nextTick(() => {
              _this.waterlist = slist
            })
            break;
          case "topic/mqtt/oilpollution_check":
            var slist = res.data[0].oilpollutionmonitoring
            _this.$nextTick(() => {
              _this.choice = slist
            })

            break;
          case "topic/mqtt/smoke_check":
            var slist = res.data
            _this.$nextTick(() => {
              _this.smokelist = slist
            })
            break;
          case "topic/mqtt/waterlevel_check":
            var slist = res.data
            console.log('slist :>> ', slist);
            _this.$nextTick(() => {
              _this.waterlevelcheck = slist
            })
            break;
        }
      });
      // 其他异常事件
      _this.MQTT.on('reconnect', () => {
        console.info('正在重连')
      });
      _this.MQTT.on('disconnect', (error) => {
        console.info('服务器断开:', error)
      });
      _this.MQTT.on('close', () => {
        _this.MQTT.end();
      });
    },
  },
  beforeDestroy() {
    if (this.MQTT.end) this.MQTT.end();
  },
};



